@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background-color: #f6f7fb;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


main {
  margin-top: 100px;
  height: 200vh;
}
.page-wrapper {
  width: 100%;
  box-sizing: border-box;
  padding: 0 8% 4%;
}
.page-wrapper > h1 {
  margin: 0;
}
.order-wrapper {
  width: 100%;
  display: flex;
  align-items: flex-start;
  padding: 24px 0;
}
.product-wrapper {
  width: 100%;
  display: flex;
  align-items: flex-start;
  padding: 24px 0;
}
.filters-section {
  width: 180px;
}
.filters-section > h3 {
  margin: 0;
}
.filters-wrapper {
  display: flex;
  flex-direction: column;
}
.filter-checkbox {
  margin: 6px 0;
  cursor: pointer;
}
.filter-checkbox > input {
  margin-right: 12px;
}
.order-list {
  width: 100%;
}
.order-table {
  width: 100%;
  border-collapse: collapse;
}
.order-table > tr {
  vertical-align: middle;
}
.order-table th,
.order-table td {
  text-align: left;
  padding: 12px 24px;
  font-size: 14px;
}
td:nth-of-type(4) {
  min-width: 150px;
}
tr td {
  background-color: #fff;
  border-bottom: 4px solid #f6f7fb;
}
.cell-primary {
  color: rgba(0, 0, 0, 0.8);
  font-weight: 500;
}
.cell-secondary {
  color: #8c8c8c;
}

.users-wrapper {
  flex-direction: column;
}
.search-form {
  width: 700px;
  display: flex;
  align-items: center;
  margin-bottom: 48px;
}
.search {
  width: calc(100% - 100px);
  padding: 14px 24px;
  font-size: 15px;
  border: 0;
  border-bottom: 2px solid #8c8c8c;
  margin: 0 36px 0 0;
  outline: none;
}
.reset {
  display: inline-block;
  font-family: "Poppins", sans-serif;
  border-radius: 30px;
  padding: 14px 36px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  background-color: #20b883;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}
.search::-webkit-input-placeholder {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}
.search:-ms-input-placeholder {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}
.search::placeholder {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}
.users-list {
  width: 100%;
}
.users-table {
  width: 100%;
  border-collapse: collapse;
}
.users-table > tr {
  vertical-align: middle;
}
.users-table th,
.users-table td {
  text-align: left;
  padding: 12px 24px;
  font-size: 14px;
}
td:nth-of-type(4) {
  min-width: auto;
}

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 48px;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  box-shadow: 0 2px 4px #ccc;
  background-color: #fff;
}
.left-part,
.logo {
  display: flex;
  align-items: center;
}
a {
  text-decoration: none;
  color: #000;
}
.logo {
  margin-right: 36px;
}
.logo > img {
  width: 24px;
  height: 28px;
  margin-right: 8px;
}
.logo > p {
  font-size: 24px;
  letter-spacing: -1px;
  font-weight: 500;
  margin: 0;
  color: rgba(0, 0, 0, 0.8);
}
.nav-bar > a {
  display: inline-block;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.8);
  padding: 24px 0 16px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.6px;
  margin: 0 10px;
}
.nav-bar > a.active {
  color: #20b883;
  border-bottom: 3px solid #20b883;
}

.button {
  position: absolute;
  cursor: pointer;
  right: 40px;
  display: inline-block;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.6);
  padding: 24px 0 16px;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.2px;
  margin: 0 10px;
  outline: none;
  border: none;
  background-color: #fff;
}

.login-form {
  width: 100%;
  max-width: 500px;
  border-radius: 6px;
  background-color: #fff;
  margin: 0 auto;
  padding: 32px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 6px #ccc;
}
.login-form > h1 {
  letter-spacing: 1px;
  font-weight: 500;
  margin: 0 0 24px;
}
.input-field {
  width: 100%;
  padding-top: 16px;

  box-sizing: border-box;
  border: 0;
  border-bottom: 2px solid #8c8c8c;
  margin-bottom: 16px;
  outline: none;
}
.input-field::-webkit-input-placeholder {
  font-size: 14px;
}
.input-field:-ms-input-placeholder {
  font-size: 14px;
}
.input-field::placeholder {
  font-size: 14px;
}
.submit-btn {
  display: inline-block;
  border-radius: 30px;
  padding: 14px 36px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  background-color: #20b883;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  margin-top: 24px;
}
.submit-btn[type="submit"] {
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
}

.page_404 {
  padding: 40px 0;
  background: #fff;
  font-family: "Arvo", serif;
  text-align: center;
  align-items: center;
}

.page_404 img {
  width: 100%;
}

.four_zero_four_bg {
  background-image: url(https://cdn.dribbble.com/users/285475/screenshots/2083086/dribbble_1.gif);
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
}

.four_zero_four_bg h1 {
  font-size: 80px;
}

.four_zero_four_bg h3 {
  font-size: 80px;
}

.link_404 {
  color: #fff !important;
  padding: 10px 20px;
  background: #39ac31;
  margin: 20px 0;
  display: inline-block;
  border-radius: 6px 0px 16px 0px;
}
.contant_box_404 {
  margin-top: -50px;
}

